.parking-item {
	height: fit-content;
	margin: 10px 0;
	border-radius: 5px;
	background-color: #fff;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);

	&:hover {
		transform: translateY(-5px);
		box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
	}

	.title {
		margin: 0;
		overflow: hidden;
		text-overflow: ellipsis;
		text-shadow: 0 0 4px rgba(0, 0, 0, 0.9);
		white-space: nowrap;
		&-icons {
			display: flex;
		}
		&-location-icon {
			margin-right: 1rem;
			color: #00008b;
			font-size: 1.6rem;
		}
		&-favorite-icon {
			font-size: 1.6rem;
			cursor: pointer;
			&.active {
				color: #ffd700;
			}
		}
		&-container {
			display: flex;
			justify-content: space-between;
			padding: 10px;
			background-color: #1e88e5;
			color: #fff;
			font-size: 1.1rem;
			letter-spacing: 0.0125em;
			line-height: 1.75rem;
		}
	}

	.p {
		margin: 0 10px;
		padding: 8px 0;
		color: #666;
	}

	.p-key {
		font-weight: 600;
	}

	button {
		width: 100%;
		padding: 6px 0 12px 0;
		border: none;
		border-radius: 5px;
		outline: 0;
		background-color: transparent;
		color: #111;
		font-size: 18px;
		text-align: center;
		cursor: pointer;
	}

	.fa-chevron-down {
		transform: rotate(0deg);
		transition: transform 0.7s ease-in-out;

		&.chevron-open {
			transform: rotate(180deg);
		}
	}

	.extra-content {
		overflow: hidden;
		transition: height 0.7s ease-in-out;
	}
}
